html {
	min-height: 100%;
	position: relative
}

body {
	padding-bottom: 0;
	background-color: #e4e6e9;
	min-height: 100%;
	font-family: 'Open Sans';
	font-size: 13px;
	color: #393939;
	line-height: 1.5
}

body.navbar-fixed {
	padding-top: 45px
}

body.breadcrumbs-fixed {
	padding-top: 86px
}

.main-container {
	position: static;
	padding: 0
}

.main-container:after {
	background-color: #FFF;
	bottom: 0;
	content: "";
	display: block;
	max-width: inherit;
	position: fixed;
	top: 0;
	width: 100%;
	z-index: -2
}

@media ( min-width :768px) {
	.container.main-container:after {
		-webkit-box-shadow: 0 0 0 1px rgba(0, 0, 0, 0.1);
		box-shadow: 0 0 0 1px rgba(0, 0, 0, 0.1)
	}
}

.main-container>.main-container-inner {
	position: relative
}

.container.main-container .sidebar.sidebar-fixed,.container.main-container .sidebar.sidebar-fixed:before
	{
	left: auto
}

.main-content {
	margin-left: 190px;
	margin-right: 0;
	margin-top: 0;
	min-height: 100%;
	padding: 0
}

.page-content {
	background: #fff;
	margin: 0;
	padding: 8px 20px 24px
}

.page-header {
	margin: 0 0 12px;
