	{
	display: inline-block
}

.ace-thumbnails>li>:first-child>.text {
	position: absolute;
	right: 0;
	left: 0;
	bottom: 0;
	top: 0;
	text-align: center;
	color: #FFF;
	background-color: rgba(0, 0, 0, 0.55);
	opacity: 0;
	filter: alpha(opacity = 0);
	-webkit-transition: all .2s ease;
	transition: all .2s ease
}

